@media screen and (min-width: 1121px) {
img { max-width: 100%;}
.board{
  border:1px #a236e6 solid;
  border-radius:20px;
  /* width:950px; */
  width:60%;
  /* height:700px; */
  height:85%;
  padding-top: 3%;
  margin-top:3%;
  margin-left:3%;
  background-color:#e0d2e9;
  overflow-y: auto;
  box-shadow: 2% 2% 1% #888888;
  float: left;
  text-align: left;
  padding-left: 1%;
  max-height:800px;
}
.ShoppingCart{
  border:1px #a236e6 solid;
  color: white;
  border-radius:2%;
  /* width:400px; */
  width:25%;
  /* height:700px; */
  /* height:85%; */
  text-align:center;
  padding-top: 1%;
  margin-top:3%;
  background-color:#332f2a;
  overflow-y: hidden;
  box-shadow: 2% 2% 1% #888888;
  float: left;
  margin-left: 1%;
  max-height:900px;
}
.card{
  font-size:1em;
  /* height: 350px; */
  height:35%;
  /* width: 200px; */
  width:30%;
  border:1px  white  solid;
  background-color: white;
  border-radius:10px;
  margin-left: 10px;
  float: left;
  margin-bottom: 10px;
  text-align: center !important;
}
.cardInCart{
  color:black;
  /* height: 170px; */
  height: 35%;
  /* width: 200px; */
  width: 70%;
  border: 1px #a236e6 solid;
  padding-top: 15px;
  border-radius: 15px;
  margin-left: 15%;
  font-size: 12px;
  background-color: #f7faff;
}

.btn{
font-weight:600;
}
.navcontent div{
  margin-left: 10px;
}
.title{
  font-size: 1.5em;
  text-align:center;
}
.contentContainer{
  margin-top:20px;
}
.itemTitle{
  text-shadow:#f3f3f3 1px 1px 0px, #b2b2b2 1px 2px 0;
  font-size: 23px;
  font-family: "Monospace";
}
.imageButton{
  border:0px;
  font-size:15px;
  border-radius:5px;
  margin-top:15px;
  margin-bottom: 15px;
  /* background-image:linear-gradient(to bottom right, #94b6f7, #4475d4);
  color:white; */
}

.checkout button{
  border:0px;
  font-size:1em;
  font-weight: 400;
  border-radius:5px;
  margin-top:15px;
  margin-bottom: 15px;
  /* width:130px; */
  width:35%;
  height:35px;
  color: white;
  background-color:#9918e9 solid;
}

.shoppingitem{
  /* height: 570px; */
  max-height:680px;
  height: 20%;
  overflow-y: auto;
  border-radius: 0px 0px 10px 10px;
}

.priceandbutton{
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  height: 10%;
  background-color: #f7faff;
  color: black;
  padding-top:20px;
}

.cost{
  font-size: 25px;
  background-color: #f7faff;
}

.title2{
  font-size: 25px !important;
  text-align: center;
  color: white;
  margin-bottom:10px;
}

.btn {
  border-radius: 5px;
}

/* Blue */
.info {
  border-color: #2196F3;
  color: dodgerblue
}
.option{
}
}
